@import '../../styles/variables';

.footerContacts{
    max-height: 115px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    .footerContactsTitle{
        color: $color-white;
        font-size: 16px;
        font-family: $font-default;
        font-weight: 800;
    }

    .footerContactsTel{
        font-family: $font-default;
        font-size: 14px;
        font-weight: 500;
        color: $color-white;
        margin: 15px 0;
    }
}



